"\"> <p _v-c58e2028=\"\"> 知乎是一个真诚、友善、认真、互助的社区<br _v-c58e2028=\"\"> 我们希望每一个疑惑都有解答，好的问题更容易得到好答案 <a href=//www.zhihu.com/question/19555761 target=_blank _v-c58e2028=\"\">如何提一个好问题？</a> </p> <p _v-c58e2028=\"\"> <b _v-c58e2028=\"\">提问应遵循真实、客观、简洁、明确、规范的原则</b><br _v-c58e2028=\"\"> 1、提问应尽可能简洁明了，避免「请问」「有谁知道」「谢谢！」等与问题本身无关的附加语。<br _v-c58e2028=\"\"> 2、应避免使用「为神马」「童鞋」「肿么办」等网络用语。<br _v-c58e2028=\"\"> 3、基于个人判断或传言的问题，在提问时应在补充说明里详细说明原由<br _v-c58e2028=\"\"> 4、避免太宽泛问题，如调查性的问题容易变成每个人都可以发表观点，但对其他人没有价值。<br _v-c58e2028=\"\"> </p> <p _v-c58e2028=\"\"> <b _v-c58e2028=\"\">知乎禁止这些提问</b><br _v-c58e2028=\"\"> 1、和已有问题完全重复的问题，<b _v-c58e2028=\"\">为避免重复，提问前可以先进行搜索</b><br _v-c58e2028=\"\"> 2、招聘 / 求职、交易 / 合作、寻人 / 征友、召集 / 赠送、求码类内容<br _v-c58e2028=\"\"> 3、投票类问题。例如：你支持方舟子还是韩寒？<br _v-c58e2028=\"\"> 4、作业或其他希望代为完成个人任务类的内容，以及针对个人具体病情的求医问药类问题<br _v-c58e2028=\"\"> 完整的提问建议可查看 <a href=//www.zhihu.com/question/19806261 target=_blank _v-c58e2028=\"\">知乎的提问规范有哪些？</a><br _v-c58e2028=\"\"> </p> <div class=before-ask-form _v-c58e2028=\"\"> <b _v-c58e2028=\"\">提问前请先搜索</b> <div style=\"position: relative; margin-top: 18px\" _v-c58e2028=\"\"> <input type=text class=before-ask-input placeholder=请输入你的问题 aria-autocomplete=list autocomplete=off _v-c58e2028=\"\"> </div> </div> </div> </div> </div> </div> </div> ";


